import React from 'react'
import styled from '@emotion/styled'
import Underline from './Underline'

const Title = ({ title }) => {
  const Titl = styled.div`
    margin-bottom: 3rem;
    text-align: center;
    color: var(--clr-white-1);
  `
    const UnderlinL = styled.div`
    width: 12rem;
    height: 0.1rem;
    margin-bottom: 0rem;
    margin-left: auto;
    margin-right: 0;
    transition: var(--transition);
    background: var(--clr-primary-7);
  `
    const UnderlinD = styled.div`
    width: 12rem;
    height: 0.1rem;
    margin-bottom: 0.75rem;
    margin-left: 0;
    margin-right: auto;
    transition: var(--transition);
    background: var(--clr-primary-7);
  `
  return (
    <Titl>
      <h3>{title}</h3>
      <UnderlinL />
      <UnderlinD />
    </Titl>
  )
}

export default Title