import  { React, useState, useEffect } from "react"
import { Link } from "gatsby"
// import heroImg from "../assets/images/hero.svg"
import styled from '@emotion/styled'
import Button from "./subcomponents/Button";
import Underline from "./subcomponents/Underline";
import socialLinks from "../constants/social_links"
import HeroSub from "./subcomponents/HeroSub";

export const Hero = () => {

//---------------------Functions --------------------------------------

  const Header = styled.header`
    margin-top: -5rem;
    padding-top: 5rem;
    padding-left: 3vw;
    padding-right: 3vw;
    height: 100vh;
    position: relative;
    background-color: var(--clr-primary-0);
    @media screen and (max-width: 1169px) {
      width: 100%;
      padding-left: 0;
    }
  `
  const Container = styled.section`
    width: 90vw;
    margin: 0 auto;
    max-width: var(--max-width);
    @media screen and (min-width: 992px) {
      width: 95vw;
  }
  `

  const SocialLinks = () => {
    const SocialContainer = styled.div`
      display: block;
      position: fixed;
      padding-top: 55vh;
    @media screen and (max-width: 1169px) {
      padding-top: 50vh;
    }
    @media screen and (max-width: 768px) {
      display: none;
    }
    `
    const SocialL = styled.a`
      color: var(--clr-primary-8);
      transition: var(--transition);
      :hover {
        color: var(--clr-primary-10);
      }
    `
    const Underlin = styled.div`
      width: 0.1rem;
      height: 15rem;
      margin-bottom: 0.75rem;
      margin-left: 35px;
      margin-right: auto;
      margin-top: 10px;
      transition: var(--transition);
      background: var(--clr-primary-8);
    `
    return (
      <SocialContainer>
      {socialLinks.map(link => {
            return (
            <li>
              <SocialL href={link.url} key={link.id}>
                {link.icon}
              </SocialL>
            </li>
            )
          })}
        <Underlin />
      </SocialContainer>
    )
  }

  const Email = () => {
    const EmailContainer = styled.div`
      display: block;
      position: fixed;
      margin-top: 55vh;
      margin-left: 90vw;
      a:hover {
        div{
          background-color: white !important;
        }
      }
      @media screen and (max-width: 1169px) {
      margin-top: 50vh;
    }
      @media screen and (max-width: 768px) {
      display: none;
    }
    `
    const EmailText = styled.a`
      writing-mode: vertical-rl;
      color: var(--clr-primary-8);
      font-size: 12px;
      letter-spacing: 0.1rem;
      transition: var(--transition);
      &:hover {
        color: var(--clr-primary-10);
        transition: var(--transition);
      }
    `
    const Underlin = styled.div`
      width: 0.1rem;
      height: 15rem;
      margin-bottom: 0.75rem;
      margin-left: 7px;
      margin-right: auto;
      margin-top: 10px;
      transition: var(--transition);
      background: var(--clr-primary-8);
    `
    return (
      <EmailContainer>
        <EmailText href="">juank987610@gmail.com</EmailText>
        <Underlin id="underlin"/>
      </EmailContainer>
    )
  }
  const Button = styled.div`
    background-color: transparent;
    border: 2px solid var(--clr-primary-7);
    display: flex;
    margin: 25px 0 !important;
    width: 150px;
    border-radius: 5rem;
    cursor: pointer;
    transition: var(--transition);
    text-align: center !important;
    &:hover {
      background-color: var(--clr-primary-7);
      transition: var(--transition);
      a {
        color: var(--clr-primary-1) !important;
      }
    }
  `
  const ButtonA = styled.a`
    margin: 0 auto !important;
    padding: 0.5rem 0rem !important;
    text-align: center;
    color: var(--clr-primary-10)!important;
    box-shadow: 0 0 var(--clr-primary-7)!important;
    font-weight: 700 !important;
    font-family: Poppins;
    letter-spacing: var(--spacing);
    transition: var(--transition);
  `
  const HeroCenter = styled.section`
    height: 100%;
    display: grid;
    align-items: center;
    width: 70%;
    margin: 0 auto;
    @media screen and (max-width: 768px) {
      width: 90%;
      grid-template-columns: repeat(12, 1fr);
      grid-column: 1 / span 8;
    }
  `
  const HeroInfo = styled.article`
    background: var(--clr-primary-0);
    @media screen and (max-width: 1169px) {
      grid-column: 1 / span 8;
      padding-left: 2vw;
    }
    @media screen and (min-width: 1170px) {
      grid-column: 1 / span 8;
    }
      h4 {
      color: var(--clr-primary-8);
    }
  `
  return (
    <Header>
        <SocialLinks />
        <Email />
        <HeroCenter>
          <HeroInfo>
            <HeroSub />
            <Button>
              <ButtonA href="#contact">Contact me</ButtonA>
            </Button>
          </HeroInfo>
        </HeroCenter>
    </Header>
  )
}